import React, { useEffect, useState } from "react";
import "./Banner.css";
import axios from "../../axios";
import { API_KEY, imageUrl } from "../../constants/constants";

function Banner() {
  const [movies, setMovies] = useState();

  useEffect(() => {
    axios
      .get(`trending/all/week?api_key=${API_KEY}&language=en-US`)
      .then((response) => {
        setMovies(response.data.results[Math.floor(Math.random() * 20)]);
      });
  }, []);

  return (
    <div
      style={{
        backgroundImage: `url(${movies ? imageUrl + movies.backdrop_path : ""})`
      }}
      className="banner"
    >
      <div className="content">
        <h1 className="title">{movies ? movies.title : "not-available"}</h1>
        <div className="navbar_buttons">
          <button className="button">Play</button>
          <button className="button">My List</button>
        </div>
        <h1 className="description">
          {movies ? movies.overview : "not-available"}
        </h1>
      </div>
      <div className="fade_bottom"></div>
    </div>
  );
}

export default Banner;
